import React, { useState, useEffect } from "react";

const Banner = () => {
  // สร้าง state สำหรับการแสดงภาพที่กำลังแสดง
  const [currentIndex, setCurrentIndex] = useState(0);

  const images = [
    "img_nature.jpg", // รูปแรก
    "img_snowtops.jpg", // รูปที่สอง
    "img_mountains.jpg", // รูปที่สาม
  ];

  // ฟังก์ชันสำหรับการเปลี่ยนไปยังรูปถัดไปหรือรูปก่อนหน้า
  const changeSlide = (index: number) => {
    setCurrentIndex((prevIndex) => {
      let newIndex = prevIndex + index;
      if (newIndex < 0) {
        newIndex = images.length - 1; // กลับไปที่รูปสุดท้าย
      } else if (newIndex >= images.length) {
        newIndex = 0; // ไปที่รูปแรก
      }
      return newIndex;
    });
  };

  // ฟังก์ชันสำหรับการเปลี่ยนไปยังรูปที่เลือก
  const goToSlide = (index: number) => {
    setCurrentIndex(index);
  };

  // ใช้ useEffect เพื่อทำให้รูปเลื่อนอัตโนมัติ
  useEffect(() => {
    const interval = setInterval(() => {
      changeSlide(1); // เลื่อนไปข้างหน้าอัตโนมัติทุก 3 วินาที
    }, 3000);

    return () => clearInterval(interval); // เคลียร์ interval เมื่อ component ถูก unmount
  }, []);

  return (
    <div className="w3-content w3-display-container">
      {/* รูปที่แสดง */}
      <img className="mySlides" src={images[currentIndex]} alt="banner" />

      <div className="w3-center w3-display-bottommiddle" style={{ width: "100%" }}>
        <div className="w3-left" onClick={() => changeSlide(-1)}>
          &#10094;
        </div>
        <div className="w3-right" onClick={() => changeSlide(1)}>
          &#10095;
        </div>
        {images.map((_, index) => (
          <span
            key={index}
            className={`w3-badge demo w3-border ${currentIndex === index ? "w3-white" : ""}`}
            onClick={() => goToSlide(index)}
          ></span>
        ))}
      </div>
    </div>
  );
};

export default Banner;
